import { useEffect } from "react";

export const steamAuthService = {
	useSteamAuthService: () => {
		useEffect(() => {
			if (window.location.href.includes("steamcommunity.com")) {
				try {
					const search = window.location.search.substring(1);

					const urlObj = JSON.parse(
						'{"' +
							decodeURI(search)
								.replace(/"/g, '\\"')
								.replace(/&/g, '","')
								.replace(/=/g, '":"') +
							'"}'
					);

					const getUserId = (response) => {
						const str = response["openid.claimed_id"];
						const res = decodeURIComponent(str);
						const propsArr = res.split("/");
						console.log(propsArr);

						return propsArr[propsArr.length - 1];
					};

					const userId = getUserId(urlObj);
					//TODO dispatch to store I guess and make it persisted
				} catch (error) {
					console.log(
						"No auth info was found, but url suggest there was a login try"
					);
				}
			}
		}, []);
	},
};
